/* ------------------------ MOBILE PHONE AREA ------------------------- */
@media only screen and (max-width: 767px) {
  /* ----- ALL CSS Here will only affect the appliation when it reaches the defined with(767) or lower. Usually phones. -------- */
  .phone-vanish {
    display: none;
  }

  .phone-margin-top {
    margin-top: 100px;
    margin-bottom: 40px;
  }
  .top-bar-phone {
    padding: 8px 20px;
    position: fixed;
    z-index: 5;
    width: 100%;
    background: white;
    left: 0;
    top: 0;
  }
  .phone-h1 {
    font-size: 22px;
  }

  .question {
    margin-top: 30px;
    font-size: 26px;
    /* min-height: 170px; */
  }
  .container-phone-mode-fix {
    margin-top: 20% !important;
  }

  .user-profile-photo {
    height: 60px;
    width: 60px;
    border-radius: 100%;
    border: solid 2px orange;
    /* padding:5px; */
    object-fit: cover;
    display: inline-block;
  }

  .name-area h3 {
    font-size: 13px;
    font-weight: bold;
  }
  .go-btn {
    padding: 6px 12px;
    background: #215221;
    color: white;
    cursor: pointer;
    border: 0;
    font-size: 18px;
    border-radius: 80px;
    font-weight: bold;
    text-shadow: -1px 1px 15px black;
  }

  .name-area {
    padding: 20px;
    height: 116px;
    width: 82%;
    position: fixed;
    top: 44vh;
    left: 9%;
    border-radius: 12px;
  }
  .up-div h3 {
    font-size: 21px;
    color: antiquewhite;
    text-shadow: 1px 2px 2px #ca6e59;
  }
  .up-div > h1 {
    font-size: 35px !important;
    margin-top: 30%;
  }
  .circle {
    height: 160px;
    width: 160px;
    border-radius: 100%;
    position: fixed;
    top: 41vh;
    left: 29%;
    cursor: pointer;
  }
  .circle::after {
    content: "";
    color: white;
    position: absolute;
    top: 5vh;
    left: 39%;
    /* font-size: 33px; */
    color: #deabab;
    border: solid 36px transparent;
    border-left-width: 10px;
    border-left-width: 20px;
    border-left-color: black;
    border-left-width: 55px;
  }
  .down-div .content p {
    margin-top: 33%;
    font-size: 15px;
    color: black;
    /* font-weight: bold; */
    text-shadow: 0px 0px 2px #90847b;
  }
  .down-div .content {
    padding-top: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
  }

  .name-box {
    margin-top: 30px;
    /* padding: 20px; */
    width: 81%;
    font-size: 17px;
    border: 0px;
    background: white;
    color: black;
    font-weight: bold;
    text-transform: capitalize;
    display: inline-bloc;
  }
  .complete-modal-body {
    z-index: 12;
    background: white;
    margin: 7% auto;
    width: 95%;
    max-height: 630px;
    border-radius: 15px;
  }
}

/* ------------------------------------ EXCLUSIVE PC MODE ------------------------ */

@media only screen and (min-width: 768px) {
  /* ------------ All css here will only affect the application when in pc mode */
  .pc-vanish {
    display: none;
  }
  .complete-modal-body {
    z-index: 12;
    background: white;
    margin: 7% auto;
    width: 95%;
    max-height: 630px;
    border-radius: 15px;
  }
  .circle::after {
    content: "";
    color: white;
    position: absolute;
    top: 10vh;
    left: 39%;
    font-size: 40px;
    color: #deabab;
    border: solid 50px transparent;
    border-left-width: 10px;
    border-left-width: 20px;
    border-left-color: black;
    border-left-width: 87px;
  }

  .circle {
    height: 300px;
    width: 300px;
    border-radius: 100%;
    position: fixed;
    top: 35vh;
    left: 40%;
    cursor: pointer;
  }
  .down-div .content {
    padding-top: 20px;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
  }

  .down-div .content p {
    margin-top: 22%;
    font-size: 24px;
    color: black;
    /* font-weight: bold; */
    text-shadow: 0px 0px 2px #90847b;
    /* font-style: italic; */
  }

  .name-area {
    padding: 20px;
    height: 146px;
    width: 35%;
    position: fixed;
    top: 43vh;
    left: 34%;
    border-radius: 12px;
  }
  .name-area h3 {
    font-size: 21px;
    font-weight: bold;
  }

  .name-box {
    margin-top: 30px;
    /* padding: 20px; */
    width: 88%;
    font-size: 26px;
    border: 0px;
    background: white;
    color: black;
    font-weight: bold;
    text-transform: capitalize;
    display: inline-block;
  }
  .go-btn {
    padding: 10px 16px;
    background: #215221;
    color: white;
    cursor: pointer;
    border: 0;
    font-size: 18px;
    border-radius: 80px;
    font-weight: bold;
    text-shadow: -1px 1px 15px black;
  }

  .user-profile-photo {
    height: 100px;
    width: 100px;
    border-radius: 100%;
    border: solid 2px orange;
    /* padding:5px; */
    object-fit: cover;
  }

  .question {
    min-height: 200px;
  }
  .complete-modal-body {
    z-index: 12;
    background: white;
    margin: 7% auto;
    width: 65%;
    max-height: 630px;
    border-radius: 15px;
  }
}

/* ---------------------- GENERAL CSS DEFINITTIONS --------------------------- */
.feedback {
  font-size: 16px;
  font-weight: bold;
  color: green;
}

.finish-btn {
  color: white;
  font-weight: bold;
  background: green;
  border: 0;
  box-shadow: 0;
  padding: 12px 20px;
  width: 100%;
  border-radius: 6px;
}

.finish-btn:hover {
  background: black;
  transition: 0.2s ease-out;
}

.review-work-btn {
  background: orange;
  color: white;
  font-weight: bold;
  padding: 12px 20px;
  border-radius: 6px;
  border: 0px;
  box-shadow: 0;
}

.review-work-btn:hover {
  background: green;
  transition: 0.2s ease-out;
}
.complete-modal-container {
  display: flex;
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: 10;
}

.complete-overlay {
  background: black;
  height: 100vh;
  width: 100%;
  opacity: 0.6;
  position: absolute;
  z-index: 11;
}

.text-entry-box {
  background: #3c3d5f;
  color: orange;
  font-size: 17px;
  white-space: pre-wrap;
  width: 100%;
  border-radius: 13px;
  font-weight: bold;
  padding: 25px;
  border: solid 3px orange;
  resize: none;
  min-height: 238px;
}

.text-entry-box:focus {
  outline: 0;
}

.text-entry-box::placeholder {
  font-style: italic;
  color: whitesmoke;
}

.chosen-answer-wrong {
  background: maroon;
  color: white;
  cursor: pointer;
  border-color: maroon !important;
}
.chosen-answer-right {
  background: green;
  color: white;
  border: white;
  cursor: pointer;
  border-color: green !important;
}

.chosen-answer-active {
  background: orange;
  color: white;
  border: white;
  cursor: pointer;
  border-color: orange !important;
}
.chosen-answer-active:hover {
  transform: scale(1.02);
  transition: 0.2s ease-out;
  border-color: orange !important;
}

.top-line {
  height: 10px;
  background: orange;
  width: 100%;
  border-style: double;
  border-color: white;
}

.round-btns {
  margin: 10px;
  display: inline-block;
  padding: 10px 15px;
  text-align: center;
  border-radius: 55px;
  background: white;
  border: 0;
  background: black;
  color: white;
}
.raw-score {
  font-size: 7rem;
  color: green;
  margin-top: 15px;
  font-weight: bold;
}

.round-btns:hover {
  background: orange;
  color: white;
  transition: 0.2s ease-out;
}
.timer-progress-bar {
  width: 97%;
  background: orange;
  height: 20px;
  display: inline-block;
  border-radius: 55px;
  margin-left: 5px;
}

.answer-selected {
  border-color: green !important;
  color: green !important;
}

.answer-wrong {
  border-color: maroon !important;
  color: maroon !important;
}

.one-answer {
  font-size: 22px;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 6px;
  border: solid 2px white;
  margin-top: 5px;
  margin-bottom: 5px;
}
.one-answer:hover {
  border-color: orange;
  transform: scale(1.02);
  transition: 0.2s ease-out;
}
.email-box {
  border: solid 0px #efefef;
  display: block;
  border-bottom-width: 2px;
  font-size: 15px;
  color: black;
  margin-bottom: 5px;
  width: 100%;
}
.textarea {
  width: 100%;
  resize: none;
  display: block;
  color: black;
  font-size: 15px;
  margin-top: 5px;
  margin-bottom: 10px;
  border: solid 0px #efefef;
  border-bottom-width: 2px;
}

.send-btn {
  background: orange;
  color: white;
  width: 90%;
  padding: 12px;
  position: absolute;
  bottom: 0px;
  border: 0;
  left: 15px;
  border-bottom-right-radius: 10px;
  border-bottom-left-radius: 10px;
  font-weight: bold;
}

.send-btn:hover {
  background: black;
  transition: 0.2s ease-out;
}

.remove-outline:focus {
  outline: 0;
}

.contact-us-box {
  height: 500px;
  border-radius: 10px;
}

.one-question {
  cursor: pointer;
  width: 100%;
  padding: 10px;
  text-align: center;
  border-radius: 5px;
  margin: 5px 0px;
}
.one-question > p {
  font-size: 14px;
  font-weight: bold;
  margin-bottom: 0px;
  text-transform: uppercase;
}
.q-default {
  /* color: grey;
  border: solid 2px grey; */
  color: black;
  border: solid 2px black;
}
.q-default:hover {
  transform: scale(1.01);
  transition: 0.2s ease-out;
}

.q-in-motion {
  color: #c58002;
  border: solid 2px #c58002;
}
.q-completed {
  color: black;
  border: solid 2px black;
}
.q-completed > p {
  color: black;
}

.q-active {
  color: orange;
  border: solid 2px orange;
}
.q-active > p {
  color: orange;
}

.custom-card > p {
  font-size: 13px;
  font-weight: bold;
  color: black;
  margin-top: 6px;
}

.custom-card {
  text-align: center;
  padding: 15px 15px 5px 15px;
  background: white;
  border-radius: 8px;
}
.c-container {
  height: 100vh;
  width: 100%;
  overflow-y: hidden;
}
.up-div {
  padding: 50px;
  text-align: center;
  background: #ffbe7b;
  height: 50vh;
}

.up-div > h1 {
  font-size: 4rem;
  color: #1d1d1d;
  text-shadow: 0px 1px 3px #6b5a1d73;
}
.up-div h3 {
  font-size: 1.8rem;
  color: antiquewhite;
  text-shadow: 1px 2px 2px #ca6e59;
}

.down-div {
  display: flex;
  /* background: #ffc107; */
  background: white;
  height: 50vh;
}

.go-btn:hover {
  background: #d69215;
  transition: 0.2s ease-out;
}
.go-btn:focus {
  outline: 0;
}

.name-box:focus {
  outline: 0;
}
.name-box::placeholder {
  color: black;
}

.circle:hover {
  transform: scale(1.1);
  transition: 0.3s ease-out;
}

.circle:active {
  transform: scale(2);
  transition: 0.2s ease-out;
  background: white;
}

.lift {
  box-shadow: 0 2px 0px 0 rgb(0 0 0 / 0%), 0 3px 46px 0 rgb(0 0 0 / 26%);
  -webkit-box-shadow: 0 2px 0px 0 rgb(0 0 0 / 0%), 0 3px 46px 0 rgb(0 0 0 / 26%);
}

.lift-slightly {
  box-shadow: 0 1px 0px 0 rgba(0, 0, 0, -1.84), 0 2px 10px 0 rgba(0, 0, 0, 0.1);
  -webkit-box-shadow: 0 1px 0px 0 rgba(0, 0, 0, -1.84),
    0 2px 10px 0 rgba(0, 0, 0, 0.1);
}
